import React from 'react'



const Nav = ({ prevDay, nextDay, date, loading, counter, apiData, fetchRandomPhoto, fetchPhotoData, fetchTodayPhoto, setApiData, setCounter }) => {
  
  const reset = () => {
    setApiData([])
    setCounter(1);
    fetchTodayPhoto();
    fetchPhotoData();
  }


  return (
    <>
      <div className="links">

        <div>
      <p className="random">Get Random Photo</p>
          <i className="fas fa-random"
            onClick={ fetchRandomPhoto}></i>
        </div>
        <div>
      <p className="reset">Reset</p>
          <i className="fas fa-step-backward"
            onClick={ reset }></i>
        </div>
      </div>

      <div className="date">
        <i
          className={`fas fa-chevron-left ${
            loading || apiData.length === counter ? "loading" : ""
          }`}
          onClick={prevDay}
        ></i>
        <p>{date}</p>
        <i
          className={`fas fa-chevron-right ${
            loading || counter === 1 ? "loading" : ""
          }`}
          onClick={nextDay}
        ></i>
      </div>
    </>
  );
}

export default Nav
