import React from 'react';

const Image = ({photoData : { media_type, url, copyright, title, explanation }}) => {

                return (
                  <div>
                    <div className="container">
                      {media_type === "image" ? (
                        <div className="photo">
                          <img src={url} alt={title} className="image" />

                          {copyright ? (
                            <h6 className="copyright">
                              Image Credit &amp; Copyright :{copyright}
                            </h6>
                          ) : (
                            <h6 className="copyright">
                              'No Image Credit Available'
                            </h6>
                          )}
                        </div>
                      ) : (
                        <div className="iframe-container">
                          <iframe
                            title="space-video"
                            width="560"
                            height="315"
                            src={url}
                            frameBorder="0"
                            allow="encrypted-media"
                            allowFullScreen
                            className="video"
                          />
                        </div>
                      )}
                      <div className="text">
                        <h2 className="title">{title}</h2>
                        <p className="explanation">{explanation}</p>
                      </div>
                    </div>
                  </div>
                );
}

export default Image
