import React from "react";
import Image from "./Components/Image";
import Nav from "./Components/Nav";
import { useEffect, useState } from "react";

function App() {
  //State
  const [photoData, setPhotoData] = useState(null);
  const [apiData, setApiData] = useState(null);
  const [counter, setCounter] = useState(1);
  const [isLoading, setIsLoading] = useState(true);

  //Today's Date
  const getToday = () => {
    let today = new Date();
    let dd = today.getDate();
    let mm = today.getMonth() + 1;
    const yyyy = today.getFullYear();
    if (dd < 10) {
      dd = `0${dd}`;
    }
    if (mm < 10) {
      mm = `0${mm}`;
    }
    return `${yyyy}-${mm}-${dd}`;
  };

  const getYear = () => {
    let today = new Date();
    return today.getFullYear();
  };

  //Change Day Functions
  const prevDay = () => {
    if (counter < apiData.length) setCounter(counter + 1);
  };
  const nextDay = () => {
    if (counter > 1) setCounter(counter - 1);
  };

  //Get Random Photo
  const fetchRandomPhoto = async () => {
    const res = await fetch(
      "https://api.nasa.gov/planetary/apod?api_key=GgFMJL2iiMuxyx7qu1GXon4DFOpC9KHkvW6w6tY9&count=1"
    );
    const data = [await res.json()];
    setApiData(data[0]);
  };
  let today = getToday();
  let year = getYear();

  const fetchPhotoData = async () => {
    const res = await fetch(
      `https://api.nasa.gov/planetary/apod?api_key=GgFMJL2iiMuxyx7qu1GXon4DFOpC9KHkvW6w6tY9&start_date=${year}-01-01&end_date=${today}`
    );
    const data = await res.json();
    setIsLoading(false);
    setApiData(data);
  };

  const fetchTodayPhoto = async () => {
    const res = await fetch(
      "https://api.nasa.gov/planetary/apod?api_key=GgFMJL2iiMuxyx7qu1GXon4DFOpC9KHkvW6w6tY9"
    );
    const data = [await res.json()];
    setApiData(data);
  }


  //On Load, get/set Data
  useEffect(() => {
    fetchTodayPhoto();
    fetchPhotoData();
  }, []);

  //Update Data
  useEffect(() => {
    if (apiData) {
      setPhotoData(apiData[apiData.length - counter]);
    }
  }, [apiData, counter]);

  //Loader
  if (!photoData) {
    return <div>No Image Available</div>;
  }

  return (
    <div className="App">
      <Nav date={photoData.date} prevDay={prevDay} nextDay={nextDay}
        loading={isLoading}
        setCounter={ setCounter}
        counter={counter}
        apiData={apiData}
        setApiData={setApiData}
        fetchRandomPhoto={fetchRandomPhoto}
        fetchPhotoData={fetchPhotoData}
        fetchTodayPhoto={fetchTodayPhoto}
        />
      <Image photoData={photoData} />
    </div>
  );
}

export default App;
